mBAAA;EACA,8BAAA;EACA,qBAAA;EACA,+BAAA;EACA,mBAAA;EACA,cAAA;EACA,aAAA;EACA,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,kBAAA;AAhFZ;;AAyFQ;EACI,mBAAA;AAtFZ;AAyFQ;EACI,kBAAA;AAvFZ;AA0FQ;EACI,kBAAA;AAxFZ;AA2FQ;EACI,mBAAA;AAzFZ;;AAgGA;EACI,yDAAA;EACA,4BAAA;EACA,0BAAA;EACA,sBAAA;AA7FJ;AA+FI;EAEI,cAAA;AA9FR;AAgGQ;EACI,kBAAA;EACA,8BAAA;EACA,qBAAA;EACA,8BAAA;EACA,mBAAA;EACA,aAAA;EACA,mBAAA;AA9FZ;AAkGgB;;EAEI,WAAA;EACA,aAAA;EACA,kCAAA;EACA,kBAAA;EACA,YAAA;AAhGpB;AAmGgB;EACI,qBAAA;EACA,yBAAA;EACA,+CAAA;EACA,gBAAA;EACA,kBAAA;EACA,iBAAA;EACA,mBAAA;AAjGpB;AAoGgB;EACI,mBAAA;AAlGpB;AAuGQ;EACI,mBAAA;EACA,+FAAA;EACA,4BAAA;EACA,aAAA;AArGZ;AAuGY;;;EAGI,mBAAA;AArGhB;AAyGQ;EACI,kBAAA;AAvGZ;AAyGY;EACI,uBAAA;AAvGhB;AA2GQ;EACI,kBAAA;AAzGZ;AA2GY;EACI,uBAAA;AAzGhB;AA6GQ;EACI,kBAAA;AA3GZ;AA6GY;EACI,uBAAA;AA3GhB;AA+GQ;EACI,mBAAA;AA7GZ;AA+GY;EACI,WAAA;EACA,YAAA;EACA,UAAA;EACA,SAAA;AA7GhB;;AA0HA;EACI,oFAAA;AAvHJ;AA0HQ;EACI,kBAAA;AAxHZ;AA2HQ;EACI,kBAAA;AAzHZ;AA4HQ;EACI,mBAAA;AA1HZ;AA6HQ;EACI,mBAAA;AA3HZ;AA+HI;EACI,aAAA;EACA,sBAAA;AA7HR;AA+HQ;EACI,mBAAA;EACA,gCAAA;AA7HZ;AA+HY;EACI,qBAAA;AA7HhB;AAkII;;;EAGI,kBAAA;EACA,SAAA;AAhIR;AAmII;;EAEI,mBAAA;EACA,SAAA;AAjIR;AAoII;EACI,gCAAA;EACA,kBAAA;EACA,aAAA;EACA,mBAAA;EACA,WAAA;EACA,aAAA;EACA,qCAAA;EACA,uBAAA;EACA,4BAAA;AAlIR;AAoIQ;EACI,kBAAA;AAlIZ;AAqIQ;EACI,kBAAA;AAnIZ;AAsIQ;EACI,kBAAA;AApIZ;AAuIQ;EACI,kBAAA;AArIZ;AAwIQ;EACI,WAAA;EACA,YAAA;EACA,gBAAA;EACA,SAAA;EACA,qBAAA;AAtIZ;;AA6IA;EACI,yDAAA;EACA,2BAAA;EACA,sBAAA;EACA,4BAAA;AA1IJ;AA8IQ;EACI,mBAAA;EACA,8BAAA;EACA,aAAA;EACA,mBAAA;AA5IZ;AA8IY;;EAEI,WAAA;EACA,aAAA;EACA,kCAAA;EACA,kBAAA;EACA,YAAA;AA5IhB;AA+IY;EACI,qBAAA;EACA,yBAAA;EACA,+CAAA;EACA,gBAAA;EACA,kBAAA;EACA,iBAAA;EACA,mBAAA;AA7IhB;AAgJY;EACI,mBAAA;AA9IhB;AAkJQ;;;;;EAKI,qBAAA;AAhJZ","sourcesContent":["// ------------------------------ Fonts ------------------------------\r\n\r\n@font-face {\r\n    font-family: haboro-contrast-normal, sans-serif;\r\n    font-weight: 300;\r\n    font-style: normal;\r\n    src: url(\"https://use.typekit.net/axk6esy.css\");\r\n}\r\n\r\n@font-face {\r\n    font-family: forma-djr-micro, sans-serif;\r\n    font-weight: 700;\r\n    font-style: normal;\r\n    src: url(\"https://use.typekit.net/axk6esy.css\");\r\n}\r\n\r\n// ------------------------------ Variables ------------------------------\r\n\r\n:root {\r\n    // Colors\r\n    --purple-dark: #780CD0;\r\n    --purple: #B361F6;\r\n    --purple-light: #CE99F9;\r\n\r\n    --blue-dark: #0C23D0;\r\n    --blue: #6071F6;\r\n    --blue-light: #99A4F9;\r\n\r\n    --accent: #F6B260;\r\n    --grey: #C9D4DA;\r\n    --off-white: #FBFBFB;\r\n    --white: #FFFFFF;\r\n\r\n    --heading: #0A0111;\r\n    --text: #666666;\r\n\r\n    // Font type\r\n    --body-font: 'haboro-contrast-normal, sans-serif';\r\n    --heading-font: 'forma-djr-micro, sans-serif';\r\n\r\n    // Font size\r\n    --font-size: 20px;\r\n\r\n    // Shadows\r\n    --main-shadow:\r\n        0 4px 24px 0 rgb(103 151 255 / 10%), 0 12px 64px 0 rgb(103 151 255 / 10%);\r\n    --hover-shadow:\r\n        0 4px 24px 0 rgb(103 151 255 / 25%), 0 12px 64px 0 rgb(103 151 255 / 25%);\r\n\r\n    // Animation\r\n    --animate-duration: 1s;\r\n    --animate-delay: 1s;\r\n    --animate-repeat: 1;\r\n}\r\n\r\n*,\r\n*::before,\r\n*::after {\r\n    box-sizing: border-box;\r\n    text-decoration: none;\r\n    list-style: none;\r\n    margin-top: 0;\r\n    margin-left: 0;\r\n    margin-right: 0;\r\n    padding: 0;\r\n}\r\n\r\nhtml {\r\n    box-sizing: inherit;\r\n    scroll-behavior: smooth;\r\n    margin: auto;\r\n\r\n}\r\n\r\nbody {\r\n    background-color: var(--off-white);\r\n    font-family: haboro-contrast-normal, sans-serif;\r\n    font-weight: 300;\r\n    font-style: normal;\r\n    font-size: var(--font-size);\r\n    color: var(--text);\r\n    line-height: 1.6;\r\n    user-select: none;\r\n    margin: auto;\r\n}\r\n\r\n// ------------------------------ Fonts ------------------------------\r\n\r\nh1,\r\nh2,\r\nh3,\r\nh4 {\r\n    font-family: forma-djr-micro, sans-serif;\r\n    font-weight: 700;\r\n    font-style: normal;\r\n    line-height: 1.1;\r\n    color: var(--heading);\r\n}\r\n\r\nh1 {\r\n    font-size: 4.209rem;\r\n}\r\n\r\nh2 {\r\n    font-size: 3.157rem;\r\n}\r\n\r\nh3 {\r\n    font-size: 2.369rem;\r\n}\r\n\r\nh4 {\r\n    font-size: 1.777rem;\r\n}\r\n\r\np {\r\n    font-family: haboro-contrast-normal, sans-serif;\r\n    font-weight: 300;\r\n    font-style: normal;\r\n}\r\n\r\n.bold_paragraph {\r\n    color: var(--white);\r\n    font-weight: 700;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n.bold_paragraph_dark {\r\n    color: var(--heading);\r\n    font-weight: 700;\r\n}\r\n\r\n// ------------------------------ Buttons ------------------------------\r\n\r\nbutton {\r\n    text-transform: uppercase;\r\n    font-family: haboro-contrast-normal, sans-serif;\r\n    font-weight: 500;\r\n    font-style: normal;\r\n    font-size: var(--font-size);\r\n    letter-spacing: 3px;\r\n    color: var(--white);\r\n    padding: 15px 40px;\r\n    border-radius: 10px;\r\n    border-width: 0px;\r\n    box-shadow: var(--main-shadow);\r\n    transition: all 0.3s ease-in-out;\r\n}\r\n\r\nbutton:hover {\r\n    box-shadow: var(--hover-shadow);\r\n}\r\n\r\n.button_purple {\r\n    background-color: var(--purple);\r\n\r\n    &:hover {\r\n        background-color: rgba(179, 97, 246, 0.8);\r\n        cursor: pointer;\r\n    }\r\n}\r\n\r\n.button_black {\r\n    background-color: var(--heading);\r\n\r\n    &:hover {\r\n        background-color: rgba(0, 0, 0, 0.8);\r\n        cursor: pointer;\r\n    }\r\n}\r\n\r\n.button_grey {\r\n    background-color: var(--grey);\r\n    color: var(--heading);\r\n\r\n    &:hover {\r\n        background-color: rgba(201, 212, 218, 0.8);\r\n        cursor: pointer;\r\n    }\r\n}\r\n\r\n.button_blue {\r\n    background-color: var(--blue-light);\r\n    color: var(--white);\r\n\r\n    &:hover {\r\n        background-color: rgba(153, 164, 249, 0.8);\r\n        cursor: pointer;\r\n    }\r\n}\r\n\r\n// ------------------------------ Sections ------------------------------\r\n\r\nsection {\r\n    width: 100%;\r\n    margin: auto;\r\n    padding: 0 120px;\r\n}\r\n\r\n.wrapper {\r\n    display: grid;\r\n    grid-template-columns: repeat(12, 1fr);\r\n    // grid-column-gap: 16px;\r\n    // grid-row-gap: 16px;\r\n    max-width: 1640px;\r\n    width: 100%;\r\n    padding: 150px 0;\r\n    margin: 0;\r\n\r\n    .heading {\r\n        grid-area: 1 / 2 / 2 / 12;\r\n        grid-template-rows: repeat(1, 1fr);\r\n        text-align: center;\r\n    }\r\n}\r\n\r\n// ------------------------------ Navbar ------------------------------\r\n\r\n#navbar {\r\n    background: url(../wave.svg);\r\n    background-repeat: no-repeat;\r\n    background-position: center;\r\n    background-size: cover;\r\n    width: 100%;\r\n    height: 200px;\r\n    box-shadow: var(--main-shadow);\r\n}\r\n\r\n.logo {\r\n    width: 60px;\r\n    height: auto;\r\n\r\n    &:hover {\r\n        cursor: pointer;\r\n    }\r\n}\r\n\r\n.contactNo {\r\n    align-items: center;\r\n    justify-content: center;\r\n    transform: translateY(-32px);\r\n    text-align: center;\r\n    color: var(--white);\r\n\r\n    p {\r\n        font-family: haboro-contrast-extended, sans-serif;\r\n        font-weight: 700;\r\n        font-style: normal;\r\n        font-size: var(--font-size);\r\n    }\r\n}\r\n\r\n.menu {\r\n    position: fixed;\r\n    width: calc(100% - 260px);\r\n    top: 50px;\r\n    // left: 120px;\r\n    // right: 120px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    padding: 10px 20px;\r\n    border-radius: 20px;\r\n    background-color: rgba(255, 255, 255, 0.8);\r\n    box-shadow: var(--main-shadow);\r\n    z-index: 10;\r\n}\r\n\r\n.menu_bottom {\r\n    position: fixed;\r\n    top: 158px;\r\n    left: 0;\r\n    width: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    font-family: haboro-contrast-extended, sans-serif;\r\n    font-weight: 700;\r\n    font-style: normal;\r\n    font-si